<script>
export default {
    data() {
        return {
            transactions: [],
            payments: [
                { type: 0, account: '**** **** **** 1234', exp: '00/00', bank: null },
                { type: 1, account: '**** **** **** 1234', exp: '00/00', bank: null },
                { type: 2, account: 'max@mustermann.de', exp: null, bank: null },
                { type: 3, account: 'max@mustermann.de', exp: null, bank: null },
                { type: 4, account: 'DE** **** **** **** 0000', exp: null, bank: 'Volksbank Münsterland' },
                { type: 5, account: 'max@mustermann.de', exp: null, bank: null },
                { type: 6, account: 'max@mustermann.de', exp: null, bank: null },
                { type: 7, account: 'max@mustermann.de', exp: null, bank: null },
            ],
            selectedPayment: 0,
            amount: 20,
        };
    },
    methods: {
        randomNo() {
            return Math.floor(Math.random() * 60) + 30;
        },
        randomTransactionType() {
            const types = ['Ausgabe', 'Einzahlung'];
            const randomIndex = Math.floor(Math.random() * types.length);
            return types[randomIndex];
        },
        randomPaymentType() {
            const types = ['PayPal', 'Banküberweisung', 'CloudCoins', 'PaySafeCard'];
            const randomIndex = Math.floor(Math.random() * types.length);
            return types[randomIndex];
        },
        selectPayment(index) {
            this.selectedPayment = index;
        },
        validateAmount() {
            if (this.amount > 100) {
                this.amount = 100;
            }
            if (this.amount < 0) {
                this.amount = 0;
            }
        }
    },
    mounted() {
        for (let i = 125; i >= 123; i--) {
            this.transactions.push({
                id: i,
                action: this.randomTransactionType(),
                cost: this.randomNo() + ".00 €",
                payment: this.randomPaymentType(),
            });
        }
    }
};
</script>


<template>
    <card-element>
        <card-body class="text-inverse m-5px bg-inverse">
            <div class="row">
                <div class="col-12 text-center">
                    <div class="opacity-5 mb-2">
                        <i class="fa fa-landmark fa-4x"></i>
                    </div>
                    <h4>Aktuelles Guthaben</h4>
                    <h1>650,72€</h1>
                </div>
                <div class="col-12">
                    <div class="row">
                        <div class="col-6 text-center">
                            <a href="#modalDeposit" data-bs-toggle="modal" class="btn btn-outline-theme btn-lg">Guthaben
                                einzahlen</a>
                        </div>
                        <div class="col-6 text-center">
                            <a href="#modalVoucher" data-bs-toggle="modal"
                                class="btn btn-outline-theme btn-lg">Gutschein einlösen</a>
                        </div>
                    </div>
                </div>
                <hr class="my-4">
                <div class="col-12">
                    <h4>Letzte Transaktionen</h4>
                    <div class="table-responsive">
                        <table class="table">
                            <thead class="table-light">
                                <tr>
                                    <th scope="col">Bestell-Nr.</th>
                                    <th scope="col">Aktion</th>
                                    <th scope="col">Betrag</th>
                                    <th scope="col">Zahlungsmethode</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="transaction in transactions" :key="transaction.id">
                                    <th scope="row">{{ transaction.id }}</th>
                                    <td v-if="transaction.action == 'Einzahlung'"><i
                                            class="fa-solid fa-arrow-trend-up text-success"></i> {{ transaction.action
                                        }}</td>
                                    <td v-else-if="transaction.action == 'Ausgabe'"><i
                                            class="fa-solid fa-arrow-trend-down text-danger"></i> {{ transaction.action
                                        }}</td>
                                    <td v-else>{{ transaction.action }}</td>
                                    <td v-if="transaction.action == 'Einzahlung'">+{{ transaction.cost }}</td>
                                    <td v-else-if="transaction.action == 'Ausgabe'">-{{ transaction.cost }}</td>
                                    <td v-else>{{ transaction.cost }}</td>
                                    <td>{{ transaction.payment }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </card-body>
    </card-element>

    <!-- BEGIN #modalDeposit -->
    <div class="modal fade" id="modalDeposit" data-bs-backdrop="static" data-bs-keyboard="false">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Guthaben einzahlen</h5>
                </div>
                <div class="modal-body">
                    <div v-if="payments && payments.length" class="text-center">
                        <div class="row text-center justify-content-center align-items-center">
                            <div class="col-3 mb-3" v-for="(payment, index) in payments" :key="index">
                                <div class="d-flex justify-content-center align-items-center w-100 h-100 cursor-pointer bg-white rounded border"
                                    @click="selectPayment(index)" :class="{
                                        'border-primary': selectedPayment === index,
                                        'border-secondary': selectedPayment !== index,
                                        'border-3': true
                                    }">
                                    <div
                                        class="d-flex justify-content-center align-items-center w-100px h-50px overflow-hidden">
                                        <img v-if="payment.type == 0"
                                            src="/assets/img/payment/visa.svg" class="h-75 w-75"
                                            alt="Visa">
                                        <img v-else-if="payment.type == 1"
                                            src="/assets/img/payment/mastercard.svg"
                                            class="h-75 w-75" alt="MasterCard">
                                        <img v-else-if="payment.type == 2"
                                            src="/assets/img/payment/paypal.svg" class="h-75 w-75"
                                            alt="PayPal">
                                        <img v-else-if="payment.type == 3"
                                            src="/assets/img/payment/klarna.svg"
                                            class="h-75 w-75" alt="Klarna">
                                        <img v-else-if="payment.type == 4"
                                            src="/assets/img/payment/giropay.svg" class="h-75 w-75"
                                            alt="GiroPay">
                                        <img v-else-if="payment.type == 5"
                                            src="/assets/img/payment/applepay.svg"
                                            class="h-75 w-75" alt="Apple Pay">
                                        <img v-else-if="payment.type == 6"
                                            src="/assets/img/payment/googlepay.svg"
                                            class="h-75 w-75" alt="Google Pay">
                                        <img v-else src="/assets/img/payment/paysafecard.svg"
                                            class="h-75 w-75" alt="Paysafecard">
                                    </div>
                                </div>
                            </div>
                            <hr>
                        </div>
                        <div class="d-flex flex-column align-items-center text-center">
                            <label for="slider" class="form-label h4">Betrag festlegen:</label>
                            <input type="number" class="form-control form-control-lg w-100px mt-2"
                                placeholder="Betrag in €" v-model.number="amount" @input="validateAmount" min="0">
                        </div>
                    </div>
                    <div v-else class="mb-3 text-center justify-content-center align-items-center">
                        <img src="https://upload.wikimedia.org/wikipedia/commons/a/af/Tux.png" alt="Image">
                        <h2>Sieht ganz schön leer aus!</h2>
                        <small>Füge eine Zahlungsmethode hinzu, bevor du Guthaben einzahlen kannst.</small>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-default" data-bs-dismiss="modal">Abbrechen</button>
                    <button type="button" class="btn btn-outline-theme" :disabled="!(payments && payments.length)">
                        Transaktion durchführen
                    </button>
                </div>
            </div>
        </div>
    </div>
    <!-- END #modalDeposit -->

    <!-- BEGIN #modalVoucher -->
    <div class="modal fade" id="modalVoucher" data-bs-backdrop="static" data-bs-keyboard="false">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Gutschein einlösen</h5>
                </div>
                <div class="modal-body">
                    <div class="d-flex flex-column align-items-center text-center">
                        <label for="vouchercode" class="form-label">Bitte gebe hier deinen Gutscheincode ein:</label>
                        <input type="text" class="form-control w-200px text-center mt-2" id="vouchercode"
                            placeholder="XXXX-XXXX-XXXX-XXXX"
                            pattern="[A-Za-z0-9]{4}-[A-Za-z0-9]{4}-[A-Za-z0-9]{4}-[A-Za-z0-9]{4}">
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-default" data-bs-dismiss="modal">Abbrechen</button>
                    <button type="button" class="btn btn-outline-theme">Überprüfen & einlösen</button>
                </div>
            </div>
        </div>
    </div>
    <!-- END #modalVoucher -->
</template>
